.toggle {
  cursor: pointer;
  flex-wrap: wrap;
  flex: 1;
  align-items: center;
  display: flex;
  position: relative;
}

.toggle__input:checked ~ .toggle__fake:after {
  background-color: #fff;
  border-color: #fff;
  left: 21px;
}

.toggle__fake {
  will-change: background-color;
  width: 40px;
  height: 20px;
  background-color: var(--toogle-background-color);
  border: 1px solid var(--toogle-border-color);
  border-radius: 20px;
  transition: background-color .15s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
  position: relative;
}

.toggle__fake:after {
  will-change: left;
  content: "";
  width: 16px;
  height: 16px;
  background-color: var(--accent-color);
  border: 1px solid var(--accent-color);
  border-radius: 10px;
  transition: left .1s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: 50%;
  left: 1px;
  transform: translateY(-50%);
}

.toggle__text {
